import axios from 'axios'
import { Message,MessageBox } from 'element-ui'
import store from '@/store'
import { getToken } from '@/utils/auth'
import code from '@/utils/code'
import QS from 'qs'

// create an axios instance
const service = axios.create({
  baseURL: process.env.BASE_API, // apibase_url
  timeout: 500000, // request timeout
  withCredentials: true //cookie
})
// request interceptor
service.interceptors.request.use(config => {
  config.data = QS.stringify(config.data);
  // Do something before request is sent
  if (store.getters.token) {
    config.headers['authorization'] = getToken() // token-- ['X-Token']key 
  }
  return config
}, error => {
  // Do something with request error
  console.log(error) // for debug
  Promise.reject(error)
})

// respone interceptor
service.interceptors.response.use(
  /**
   * responsecodecode
   * xmlhttprequest  error
   */
  res => {
    if(res.data.code==code.LOGIN_SUCC||res.data.code==code.SUCC){
      //jsonpass
      return res;
    }else{
      if(res.data.code == code.UNAUTHEN || res.data.code == code.SESSION_TIMOUT){
        //
        MessageBox.confirm('', '',
          {confirmButtonText: '',cancelButtonText: '',type: 'warning'}).then(() => {
          store.dispatch('FedLogOut').then(() => {
            location.reload();// vue-router bug
          });
        })
      }else{
        //
        Message({ message: res.data.msg, type: 'error', duration: 5000});
      }
      return Promise.reject('error');
    }
  },
  /**
   * 
   */
  err => {
    console.error('request err: %o', err)// for debug
    Message({message: error.message,type: 'error',duration: 5000})
    return Promise.reject(error)
  }
)
export default service



