<template>
  <div class="addFruit">
    <el-form class="detail-form" autocomplete="on" :model="fruitForm" :rules="fruitRules" ref="fruitForm"
      label-position="left" label-width="100px">
      <div class="bread">
        <span class="btn" @click="returnPage"></span>
        <span class="line">>></span>
        <span class="btn1"></span>
      </div>
      <el-row>
        <el-col :span="8">
          <el-form-item prop="fruitName" label="">
            <el-input class="width250" name="fruitName" type="text" v-model="fruitForm.fruitName" />
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item prop="fruitType" label="">
            <el-select v-model="fruitForm.fruitType" placeholder="" :on-change="getUpdateCommodity(fruitForm.fruitType)">
              <el-option v-for="item in updateCommodity" :key="item.value" :lable="item.value" :value="item.lable"></el-option>
            </el-select>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="8">
          <el-form-item prop="fruitNum" label="">
            <el-input class="width250" name="fruitNum" type="text" v-model="fruitForm.fruitNum" />
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item prop="fruitWeight" label="">
            <el-input class="width250" name="fruitWeight" type="text" v-model="fruitForm.fruitWeight" />
          </el-form-item>
        </el-col>
        <el-col :span="8">
          <el-form-item label="">
            <el-form-item prop="fruitVipPrice" label="" label-width="66px" class="member">
              <el-input class="width100" name="fruitVipPrice" type="text" v-model="fruitForm.fruitVipPrice" />
            </el-form-item>
            <el-form-item prop="fruitPrice" label="" label-width="66px">
              <el-input class="width100" name="fruitPrice" type="text" v-model="fruitForm.fruitPrice" />
            </el-form-item>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row class="message">
        <el-col :span="24">
          <el-form-item label="">
            <el-row>
              <el-col :span="3">
                <el-form-item prop="fruitOriginPlace">
                  <p class="text"></p>
                  <el-input class="width110" name="fruitOriginPlace" type="text" v-model="fruitForm.fruitOriginPlace" />
                </el-form-item>
              </el-col>
              <el-col :span="3">
                <el-form-item prop="fruitSpecifications">
                  <p class="text"></p>
                  <el-input class="width110" name="fruitSpecifications" type="text" v-model="fruitForm.fruitSpecifications" />
                </el-form-item>
              </el-col>
              <el-col :span="3">
                <el-form-item prop="fruitPacking">
                  <p class="text"></p>
                  <el-input class="width110" name="fruitPacking" type="text" v-model="fruitForm.fruitPacking" />
                </el-form-item>
              </el-col>
              <el-col :span="4">
                <el-form-item prop="fruitQuality">
                  <p class="text"></p>
                  <el-input class="width110" name="fruitQuality" type="text" v-model="fruitForm.fruitQuality" />
                </el-form-item>
              </el-col>
              <el-col :span="3">
                <el-form-item prop="fruitStorageMode">
                  <p class="text"></p>
                  <el-input class="width110" name="fruitStorageMode" type="text" v-model="fruitForm.fruitStorageMode" />
                </el-form-item>
              </el-col>
            </el-row>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-form-item prop="fruitPictureUrl" label="">
            <el-upload ref="upload1" action :http-request="handleFile" :on-change="fruitPictureUrl_file" :multiple="false"
              :limit="1" :file-list="fruitPictureUrl" accept="image/jpeg, image/gif, image/png, image/bmp"
              :before-upload="beforeAvatarUpload" list-type="picture">
              <el-button size="small" type="primary" @click="clearUploadedImage('upload1')"></el-button>
              <div slot="tip" class="el-upload__tip"></div>
            </el-upload>
          </el-form-item>
        </el-col>
        <el-col :span="12">
          <el-form-item prop="fruitDetailCarousel" label="">
            <el-upload ref="upload2" action :http-request="handleFile" :on-change="fruitDetailCarousel_file" :multiple="true"
              :limit="3" :file-list="fruitDetailCarousel" accept="image/jpeg, image/gif, image/png, image/bmp"
              :before-upload="beforeAvatarUpload" list-type="picture">
              <el-button size="small" type="primary" @click="clearUploadedImage('upload2')"></el-button>
              <div slot="tip" class="el-upload__tip"></div>
            </el-upload>
          </el-form-item>
        </el-col>
      </el-row>
      <el-row>
        <el-col :span="12">
          <el-form-item prop="fruitDetail" label="">
            <el-upload ref="upload3" action :http-request="handleFile" :on-change="fruitDetail_file" :multiple="true"
              :limit="3" :file-list="fruitDetail" accept="image/jpeg, image/gif, image/png, image/bmp"
              :before-upload="beforeAvatarUpload" list-type="picture">
              <el-button size="small" type="primary" @click="clearUploadedImage('upload3')"></el-button>
              <div slot="tip" class="el-upload__tip"></div>
            </el-upload>
          </el-form-item>
        </el-col>
      </el-row>
      <div class="dialog-footer" style="float:right;margin-right: 212px;">
        <el-button @click="colseForm"></el-button>
        <el-button @click="addFruit" :disabled="isDisabled"></el-button>
      </div>
    </el-form>
  </div>
</template>
<script>
  import fruitApi from "@/api/fruit";
  import {validateNum,validateDecimal} from "@/utils/validate";
  import {updateCommodity} from "@/utils/commodity";
  import store from "@/store";
  import {getToken} from "@/utils/auth";
  import axios from "axios";
  export default {
    name: 'addFruit',
    data() {
      return {
        updateCommodity: updateCommodity,
        fruitForm: {
          id: null,
          fruitName: null, //
          fruitType:null,//
          updateCommodity:null,
          fruitNum: null, //
          fruitWeight: null, //
          fruitVipPrice: null, //
          fruitPrice: null, //
          fruitOriginPlace: null, //-
          fruitSpecifications: null, //-
          fruitPacking: null, //-
          fruitQuality: null, //-
          fruitStorageMode: null, //-
        },
        fruitDetail: [],
        fruitPictureUrl: [],
        fruitDetailCarousel: [],
        fruitRules: {
          // 
          fruitName: [{
            required: true,
            trigger: "blur",
            message: ""
          }],
          // 
          fruitType: [{
            required: true,
            trigger: "blur",
            message: ""
          }],
          // 
          fruitNum: [{
            required: true,
            trigger: "blur",
            message: ""
          }],
          // 
          fruitWeight: [{
            required: true,
            trigger: "blur",
            message: ""
          }],
          // 
          fruitVipPrice: [{
            required: true,
            trigger: "blur",
            message: ""
          }],
          // 
          fruitPrice: [{
            required: true,
            trigger: "blur",
            message: ""
          }]
        },
        isDisabled: false
      };
    },
    created() {
      this.initData();
    },
    methods: {
      initData() {
        //
      },
      handleFile() {},
      clearUploadedImage(type) {
        // 
        if (type === "upload1") {
          this.$refs.upload1.clearFiles();
          this.fruitPictureUrl = [];
        } else if (type === "upload2") {
          this.$refs.upload2.clearFiles();
          this.fruitDetailCarousel = [];
        } else if (type === "upload3") {
          this.$refs.upload3.clearFiles();
          this.fruitDetail = [];
        }
      },
      fruitPictureUrl_file(file, fileList) {
        //  on-change 
        this.fruitPictureUrl = fileList;
      },
      fruitDetailCarousel_file(file, fileList) {
        //  on-change 
        this.fruitDetailCarousel = fileList;
      },
      fruitDetail_file(file, fileList) {
        //  on-change 
        this.fruitDetail = fileList;
      },
      beforeAvatarUpload(file) {
        const isJPG = file.type === "image/jpeg";
        const isGIF = file.type === "image/gif";
        const isPNG = file.type === "image/png";
        const isBMP = file.type === "image/bmp";
        const isLt2M = file.size / 1024 / 1024 < 2;
        if (!isJPG && !isGIF && !isPNG && !isBMP) {
          this.$message.error("JPG/GIF/PNG/BMP !");
        }
        if (!isLt2M) {
          this.$message.error(" 2MB!");
        }
        return (isJPG || isBMP || isGIF || isPNG) && isLt2M;
      },
      // 
      getUpdateCommodity(value) {
        for(let i = 0; i < updateCommodity.length; i++){
          if(value==updateCommodity[i].lable){
            console.log(updateCommodity[i].value)
            return updateCommodity[i].value;
          }
        }
      },
      getUpdateCommodityLable(value){
        for(let i = 0; i < updateCommodity.length; i++){
          if(value==updateCommodity[i].value){
            console.log(updateCommodity[i].value)
            return updateCommodity[i].lable;
          }
        }
      },
      addFruit() {
        this.$refs.fruitForm.validate(valid => {
          if (!this.fruitDetail[0]) {
            this.$message.error("");
            return;
          }
          if (valid) {
            this.isDisabled = true;
            let formData = new FormData();
            // var formData = new window.FormData()
            //
            formData.append("fruitName", this.fruitForm.fruitName); //
            formData.append("fruitType", this.getUpdateCommodity(this.fruitForm.fruitType));//
            formData.append("fruitNum", this.fruitForm.fruitNum); //
            formData.append("fruitWeight", this.fruitForm.fruitWeight); //
            formData.append("fruitVipPrice", this.fruitForm.fruitVipPrice); //
            formData.append("fruitPrice", this.fruitForm.fruitPrice); //
            formData.append("fruitOriginPlace", this.fruitForm.fruitOriginPlace); //-
            formData.append("fruitSpecifications", this.fruitForm.fruitSpecifications); //-
            formData.append("fruitPacking", this.fruitForm.fruitPacking); //-
            formData.append("fruitQuality", this.fruitForm.fruitQuality); //-
            formData.append("fruitStorageMode", this.fruitForm.fruitStorageMode); //-
            formData.append("fruitPicture",this.fruitPictureUrl[0] ? this.fruitPictureUrl[0].raw : '');//
            for(let i = 0; i < this.fruitDetailCarousel.length; i++){
              formData.append('fruitDetailCarousels', this.fruitDetailCarousel[i] ? this.fruitDetailCarousel[i].raw : '');
            }// 
            for(let i = 0; i < this.fruitDetail.length; i++){
              formData.append('fruitDetails', this.fruitDetail[i] ? this.fruitDetail[i].raw : '');
            }
            let config = {
              headers: {
                "Content-Type": "multipart/form-data"
              }
            };
            var url = "/fruit/addFruit";
            if (store.getters.token) {
              config.headers["authorization"] = getToken(); // token-- ['X-Token']key 
            }
            axios.defaults.withCredentials = true;
            axios.post(process.env.BASE_API + url, formData, config, {
              withCredentials: true
            }).then(res => {
              console.log(res.data);
              if (res.data.code == 1000) {
                this.$message.success("");
                this.colseForm();
              } else {
                this.$message.error("");
              }
            });
          }
        });
      },
      colseForm() {
        this.$store.dispatch("delVisitedViews", this.$route);
        this.$router.go(-1);
      },
      returnPage(){
        this.$store.dispatch("delVisitedViews", this.$route);
        this.$router.go(-1);
      },
    }
  };
</script>
<style scoped>
  .member {
    margin-bottom: 10px;
  }

  .width100 {
    width: 100px;
  }

  .width110 {
    width: 110px;
  }

  .el-select,
  .width250 {
    width: 250px;
  }

  p.text {
    width: 110px;
    color: #606266;
    text-align: center;
  }
  .message {
    margin-bottom: 20px;
  }
  .bread{
    position: relative;
    width: 300px;
  }
  span{
    display: inline-block;
  }
  .btn{
    cursor: pointer;
  }
  .btn,.btn1{
    width: 100px;
    font-size: 14px;
    color: #fff;
    line-height: 26px;
    text-align: center;
    margin-bottom: 35px;
    border: 1px solid #ccc;
    background-color: #304156;
    transition:all .5s;
  }
  .btn:hover,.btn1:hover{
    background-color: #409EFF;
  }
  .line{
    font-weight: 550;
  }
</style>



