import router from './router'
import store from './store'
import { Message } from 'element-ui'
import NProgress from 'nprogress' // progress bar
import 'nprogress/nprogress.css'// progress bar style
import { getToken } from '@/utils/auth' // getToken from cookie

NProgress.configure({ showSpinner: false })// NProgress Configuration

/**
 * 
 * @param userPerms vuex
 * @param routerPerm src/router/index.jsasyncRouterMap
 * @returns {*}
 */
function hasPermission(userPerms, routerPerm) {
  //*
  if(userPerms.some(p=>p.val=="*")) return true;
  //perm
  if(!routerPerm) return true;
  //
  return userPerms.some(p=>p.val==routerPerm)
}

const loginRoute = "/login"
const whiteList = new Set([loginRoute,'/authredirect'])// no redirect whitelist

router.beforeEach((to, from, next) => {

  // NProgress.start()
  let token = getToken();
  let hasToken = token != 'undefined' && token != undefined && token !=null && token != '';

  if (hasToken) {
    // 1.token
    if (to.path == loginRoute) {
      // 1.1 token
      next({ path: '/' })
      NProgress.done() // if current page is dashboard will not trigger	afterEach hook, so manually handle it
    } else {
      // 1.2 
      if(store.getters.visitor){
        // 1.2.1 true,GetUserInforolesperms
        // visitor(src/router/index.jsperm)
        next()
      }else if (!store.getters.perms || store.getters.perms.length === 0) {
        // 1.2.2 
        // vuexvuex
        store.dispatch('GetUserInfo').then(res => {
          const perms = res.data.data.perms // note: roles must be a array! such as: [{name:'1',val:'menu:1'}]
          store.dispatch('GenerateRoutes', { perms }).then(() => { // roles
            router.addRoutes(store.getters.addRouters) // 
            next({ ...to, replace: true }) // hack addRoutes ,set the replace: true so the navigation will not leave a history record
          })
        }).catch((err) => {
          store.dispatch('FedLogOut').then(() => {
            Message.error(err || '')
            next({ path: '/' })
          })
        })
      } else {
        // 1.2.3 vuexok
        if (hasPermission(store.getters.perms, to.meta.perm)) {
          next()
        } else {
          next({ path: '/401', replace: true, query: { noGoBack: true }})
        }
      }
    }
  } else {
    // 2.token
    // 
    whiteList.has(to.path) ? next() : next(loginRoute);
    NProgress.done();
  }
})

router.afterEach(() => {
  NProgress.done() // finish progress bar
})



