<template>
  <div class="swiper-container">
    <div class="swiper-wrapper">
      <div class="swiper-slide" v-for="item in swiper" :key="item" ><img :src="item" alt=""></div>
    </div>
    <div class="swiper-pagination swiper-pagination-bullets" style="top: 164px;"></div>
    <!--  -->
	    <!-- <div class="swiper-button-prev"></div>
	    <div class="swiper-button-next"></div> -->
  </div>
</template>
<script>
  // npm install swiper --save
  import Swiper from 'swiper'
  import "swiper/css/swiper.css"

  export default {
    name: 'swiper',
    data() {
      return {
        mySwiper: null
      }
    },
    props: ['swiper'], //swipertest
    methods: {
      _initSwiper() {
        if (this.mySwiper != null) return;
        this.mySwiper = new Swiper('.swiper-container', {
         loopAdditionalSlides: 3,
            loop: true,
            speed: 2000,
            autoplay: {// 
              delay: 1000, //1
              // disableOnInteraction: false
            },
            pagination: {// 
              el: '.swiper-pagination',
              // clickable: true,
            },

        autoplayDisableOnInteraction: false,
        observer: true, // (OB//)
        observeParents: true // swiperswiper
        })
      },
      _updateSwiper() {
        this.$nextTick(() => {
          // this.mySwiper.update(true); //swiper update
         this._initSwiper(); //
        })
      },
      swiperUpdate() {
        if (this.mySwiper) { //
          this._updateSwiper(); //
        } else {
          this._initSwiper(); //
        }
      }
    },
    watch: {
      //props   swiper()
      swiper() {
        this.swiperUpdate();
      }
    },
    mounted() {
      this.swiperUpdate(); //
    },

  }
</script>

<style lang="less" scoped>
  .swiper-container {
    width: 60%;
    height: 200px;
    margin-top: 10px;
    float: left;
    .swiper-wrapper {
      width: 100%;
      height: 100%;
      .swiper-slide {
        background-size: cover;
        width: 100%;
        height: 200px;
        img {
          display: block;
          width: 100%;
          height: 100%;
        }
      }
    }
  }
</style>



