<template>
  <div class="afterSale">
    <!---->
    <el-row class="inquire">
      <el-input style="width:200px;" v-model="tableQuery.orderNo" placeholder=""></el-input>
      <span style="margin-right: 15px;"></span>
      <el-input style="width:200px;" v-model="tableQuery.fruitName" placeholder=""></el-input>
      <span style="margin-right: 15px;"></span>
      <el-tooltip class="item" content="" placement="top">
        <el-button icon="el-icon-search" circle @click="fetchData(1)"></el-button>
      </el-tooltip>
    </el-row>
    <!--  -->
    <el-tabs v-model="activeName" @tab-click="handleClick">
      <el-tab-pane label="" name="first" v-loading.body="tableLoading" element-loading-text="" fit highlight-current-row>
        <el-table :data="tableData" border>
          <el-table-column prop="orderNo" label=""></el-table-column>
          <el-table-column prop="fruitName" label=""></el-table-column>
          <el-table-column prop="orderStatus" :formatter="formatterOrderStatus" label=""></el-table-column>
          <el-table-column prop="createTime" label="" :formatter="formatterCreateTime"></el-table-column>
          <el-table-column prop="phone" label=""></el-table-column>
          <el-table-column prop="fruitStatus" label=""></el-table-column>
          <el-table-column prop="refundReason" label=""></el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              <p>{{scope.row.refundFee}}</p>
            </template>
          </el-table-column>
          <el-table-column prop="refundUrl" label="">
            <template slot-scope="scope">
              <img v-for="item in scope.row.refundUrl" :src="item" class="refundUrlImg" />
            </template>
          </el-table-column>
          <el-table-column label="" width="180">
            <template slot-scope="scope">
              <el-tooltip content="" placement="top" v-show="scope.row.orderStatus==6">
                <img src="../img/compile.png" alt="" @click="handleAmend(scope.$index,scope.row)" />
              </el-tooltip>
              <el-tooltip content="" placement="top" v-show="scope.row.orderStatus==6 || scope.row.orderStatus==5">
                <img src="../img/correct.png" alt="" @click="handleAccept(scope.$index,scope.row,1)" />
              </el-tooltip>
              <el-tooltip content="" placement="top" v-if="!hasRole(scope.row)" v-show="scope.row.orderStatus==6">
                <img src="../img/srror.png" alt="" @click="handleSrror(scope.$index,scope.row,0)" />
              </el-tooltip>
            </template>
          </el-table-column>
        </el-table>
      </el-tab-pane>
      <el-tab-pane label="" name="second" v-loading.body="tableLoading" element-loading-text="" fit highlight-current-row>
        <el-table :data="tableData" border>
          <el-table-column prop="orderNo" label=""></el-table-column>
          <el-table-column prop="fruitName" label=""></el-table-column>
          <el-table-column prop="orderStatus" :formatter="formatterOrderStatus" label=""></el-table-column>
          <el-table-column prop="createTime" label="" :formatter="formatterCreateTime"></el-table-column>
          <el-table-column prop="phone" label=""></el-table-column>
          <el-table-column prop="fruitStatus" label=""></el-table-column>
          <el-table-column prop="refundReason" label=""></el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              <p>{{scope.row.refundFee}}</p>
            </template>
          </el-table-column>
          <el-table-column prop="refundUrl" label="">
            <template slot-scope="scope">
              <img v-for="item in scope.row.refundUrl" :src="item" width="90%" height="80" class="refundUrlImg" />
            </template>
          </el-table-column>
        </el-table>
      </el-tab-pane>
      <el-tab-pane label="" name="third" v-loading.body="tableLoading" element-loading-text="" fit highlight-current-row>
        <el-table :data="tableData" border>
          <el-table-column prop="orderNo" label=""></el-table-column>
          <el-table-column prop="fruitName" label=""></el-table-column>
          <el-table-column prop="orderStatus" :formatter="formatterOrderStatus" label=""></el-table-column>
          <el-table-column prop="createTime" label="" :formatter="formatterCreateTime"></el-table-column>
          <el-table-column prop="phone" label=""></el-table-column>
          <el-table-column prop="fruitStatus" label=""></el-table-column>
          <el-table-column prop="refundReason" label=""></el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              <p>{{scope.row.refundFee}}</p>
            </template>
          </el-table-column>
          <el-table-column prop="refundUrl" label="" class="refundUrl">
           <template slot-scope="scope">
             <img v-for="item in scope.row.refundUrl" :src="item" width="90%" height="80" class="refundUrlImg" />
           </template>
          </el-table-column>
          <el-table-column label="" width="180">
            <template slot-scope="scope">
              <el-tooltip content="" placement="top" v-show="scope.row.orderStatus==6">
                <img src="../img/compile.png" alt="" @click="handleAmend(scope.$index,scope.row)" />
              </el-tooltip>
              <el-tooltip content="" placement="top">
                <img src="../img/correct.png" alt="" @click="handleAccept(scope.$index,scope.row,1)" />
              </el-tooltip>
              <el-tooltip content="" placement="top" v-if="!hasRole(scope.row)" v-show="scope.row.orderStatus==6">
                <img src="../img/srror.png" alt="" @click="handleSrror(scope.$index,scope.row,0)" />
              </el-tooltip>
            </template>
          </el-table-column>
        </el-table>
      </el-tab-pane>
    </el-tabs>
    <!-- ---begin -->
    <el-pagination background @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="tablePage.current"
      :page-sizes="[10, 20, 30, 40, 50]" :page-size="tablePage.size" layout="total, sizes, prev, pager, next, jumper"
      :total="tablePage.total">
    </el-pagination>
    <!-- ---end -->
    <!--  -->
    <el-dialog title="" :visible.sync="handleCancel" width="30%" v-dialogDrag center>
      <el-form :rules="rules" ref="dataForm" :model="temp" label-position="left" label-width="140px">
        <el-form-item label="" prop="refundFee">
          <el-input v-model="temp.refundFee"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="handleCancel = false"></el-button>
        <el-button type="primary" @click="handleAffirm()"></el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
  import afterSaleApi from '@/api/afterSale'
  import {parseTime,resetTemp} from '@/utils'
  import {pageParamNames,confirm,root} from '@/utils/constants'
  import debounce from 'lodash/debounce'
  import {billOrderStatus} from '@/utils/bill'
  import store from "@/store"
  import {getToken} from "@/utils/auth"
  import axios from "axios"
  export default {
    name: 'afterSale',
    data() {
      return {
        billOrderStatus: billOrderStatus,
        activeName: 'first',
        query: '',
        pagenum: 1,
        pagesize: 20,
        value: true,
        tableData: [],
        img: '',
        tableQuery: {
          orderNo: null,
          fruitName: null,
          isHandle: null,
        },
        tablePage: {
          current: null,
          pages: null,
          size: null,
          total: null,
        },
        orderNo: null, //
        fruitName: null, //
        orderStatus: null, //
        createTime:null,//
        phone: null, //
        fruitStatus: null, //
        refundReason: null, //
        refundFee: null, //
        refundUrl: null, //
        handleCancel: false, //
        temp: {
          idx: null, //tableData
          id: null,
          isAgree:null,
          orderNo: null, //
          fruitName: null, //
          orderStatus: null, //
          phone: null, //
          fruitStatus: null, //
          refundReason: null, //
          refundFee: null, //
          refundUrl:null,
        },
        rules: {
          content: [{
            validator: validateContent,
            trigger: 'blur'
          }],
        },
      };
      let validateContent = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else {
          callback();
        }
      };
    },
    // 
    created() {
      this.fetchData()
    },
    watch: {
      //
      'tableQuery.orderNo': debounce(function() {
        this.fetchData()
      }, 500),
      'tableQuery.fruitName': debounce(function() {
        this.fetchData()
      }, 500),
    },
    methods: {
      hasRole(row) {
        return row && row.rval == root.rval
      },
      handleClick(tab, event) {
        this.fetchData()
      },
      // 
      handleSizeChange(val) {
        this.tablePage.size = val;
        this.fetchData();
      },
      handleCurrentChange(val) {
        this.tablePage.current = val;
        this.fetchData();
      },
      // 
      fetchData() {
        this.tableLoading = true
        if(this.activeName == 'first'){
          this.tableQuery.isHandle=null
        }else if (this.activeName == 'second') {
          this.tableQuery.isHandle=1
        } else if (this.activeName == 'third') {
          this.tableQuery.isHandle=0
        }
        afterSaleApi.queryAfterSaleList(this.tableQuery, this.tablePage,).then(res => {
          console.log(res.data)
          this.tableData = res.data.data.data
          this.tableLoading = false;
          var len = this.tableData.length;
          for(var i = 0; i < len; i ++){
            if(this.tableData[i].refundUrl){
              this.tableData[i].refundUrl =  this.tableData[i].refundUrl.split(';');
            }else{
              this.tableData[i].refundUrl = [];
            }
          }
          //
          pageParamNames.forEach(name => this.$set(this.tablePage, name, res.data.data[name]))
        })
      },
      //
      formatterOrderStatus: function(row, column, value) {
        for (let i = 0; i < billOrderStatus.length; i++) {
          if (value == billOrderStatus[i].value) {
            return billOrderStatus[i].lable;
          }
        }
      },
      // 
      formatterCreateTime: function(row, column, value){
        return value.substring(0,4)+"-"+ value.substring(4,6)+"-"+ value.substring(6,8)+" "+value.substring(8,10)+":"+value.substring(10,12)+":"+value.substring(12);
      },
      // 
      formatterRefundUrl: function(row, column, value) {
        return value.split(';');
      },
      // 
      handleAmend(idx, row) {
        this.temp = Object.assign({}, row) // copy obj
        this.temp.idx = idx;
        this.handleCancel = true
        this.$nextTick(() => this.$refs['dataForm'].clearValidate())
      },
      // 
      handleAffirm() {
        this.$refs['dataForm'].validate((valid) => {
          if (!valid) return
          let formData = new FormData();
          //
          formData.append('id', this.temp.id);
          formData.append('refundFee', this.temp.refundFee);
          var url = '/orderRefund/update';
          let config = {
            headers: {
              'Content-Type': 'multipart/form-data'
            }
          };
          if (store.getters.token) {
            config.headers['authorization'] = getToken() // token-- ['X-Token']key 
          }
          axios.defaults.withCredentials = true
          axios.post(process.env.BASE_API + url, formData, config, {
            withCredentials: true
          }).then(res => {
            console.log(res.data)
            this.handleCancel = false
            if (res.data.code == 1000) {
              this.$message.success("")
              window.location.reload();
            } else {
              this.$message.error("")
            }
          });
        })
      },
      //
      //
      handleAccept(idx, row, value) {
        let massge = "";
        this.$confirm(massge, '', confirm).then(() => {
          afterSaleApi.isAgree({
            id: row.id,
            isAgree: value
          }).then(res => {
            if (res.data.code == 1000 && isAgree=='1') {
              this.$message.success('');
            } else {
              this.$message.error('');
            }
            window.location.reload();
          })
        }).catch(() => {
          this.$message.info('')
        })
      },
      //
      handleSrror(idx, row, value) {
        let massge = "";
        this.$confirm(massge, '', confirm).then(() => {
          afterSaleApi.isAgree({
            id: row.id,
            isAgree: value
          }).then(res => {
            if (res.data.code == 1000 && isAgree=='0') {
              this.$message.success('');
            } else {
              this.$message.error('');
            }
            window.location.reload();
          })
        }).catch(() => {
          this.$message.info('')
        })
      },
    }
  };
</script>
<style scoped>
  .afterSale {
    position: relative;
  }
  .inquire {
    position: absolute;
    right: 0;
    top: -66px;
    margin-bottom: 20px;
  }
  .refundUrlImg{
    display: block;
    width: 90%;
    height: 80px;
  }
  .refundUrlImg+.refundUrlImg{
    height: 0;
    transition: all .5s ease-in-out;
  }
  .el-table tr:hover .refundUrlImg+.refundUrlImg{
    height: 80px;
    margin-top: 10px;
  }
  /* .el-table__body td{
    position: relative;
  }
  .refundUrlImg:nth-of-type(2){
    position: absolute;
    left: -85%;
  } */
</style>



