import request from '@/utils/request'

export default{
  // ---------------------------()---------------------------
  queryOrderForm(queryParam,pageParam) {
    return request({
      url: '/orderInfo/list',
      method: 'post',
      data: {
        ...queryParam,
        current: pageParam.current,
        size: pageParam.size
      }
    })
  },
  // ---------------------------()---------------------------
  signedList(queryParam,pageParam) {
    return request({
      url: '/orderInfo/signedList',
      method: 'post',
      data: {
        ...queryParam,
        current: pageParam.current,
        size: pageParam.size
      }
    })
  },
  //  ---------------------------()---------------------------
  deliverGoods(data) {
    return request({
      url: '/orderInfo/deliverGoods',
      method: 'post',
      data
    })
  },
}



