<template>
  <div class="commodity">
    <!-----begin-->
    <el-row class="inquire">
      <el-input style="width:200px;" v-model="tableQuery.fruitName" placeholder=""></el-input>
      <span style="margin-right: 15px;"></span>
      <el-select style="width:200px;" v-model="tableQuery.statusValue" placeholder="">
        <el-option v-for="item in updateCommodity" :key="item.value" :lable="item.value" :value="item.lable"></el-option>
      </el-select>
      <span style="margin-right: 15px;"></span>
      <el-tooltip class="item" content="" placement="top">
        <el-button icon="el-icon-search" circle @click="fetchData(1)"></el-button>
      </el-tooltip>
    </el-row>
    <!-----end-->
    <el-tabs v-model="activeName" @tab-click="handleClick">
      <!-- ---begin -->
      <el-tab-pane label="" name="first" v-loading.body="tableLoading" element-loading-text="Loading">
        <!-- ---begin -->
        <el-button class="uploadingBtn" @click="uploadingBtn"></el-button>
        <!-- ---end -->
        <el-table :data="tableData" border>
          <el-table-column prop="fruitType" :formatter="formatterFruitType" label=""></el-table-column>
          <el-table-column prop="fruitName" label=""></el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              <p>{{scope.row.fruitPrice}}</p>
              <p>{{scope.row.fruitVipPrice}}</p>
            </template>
          </el-table-column>
          <el-table-column prop="fruitPacking" label=""></el-table-column>
          <el-table-column prop="fruitNum" label=""></el-table-column>
          <el-table-column prop="fruitOriginPlace" label="">
            <template slot-scope="scope">
              <p>{{scope.row.fruitOriginPlace}}</p>
              <p>{{scope.row.fruitSpecifications}}</p>
              <p>{{scope.row.fruitQuality}}</p>
            </template>
          </el-table-column>
          <el-table-column prop="fruitPictureUrl" label="">
            <template slot-scope="scope">
              <img :src="scope.row.fruitPictureUrl" width="90%" height="80" />
            </template>
          </el-table-column>
          <el-table-column prop="status" label="">
            <template  slot-scope="scope" >
                <el-tag :type="scope.row.status=='1' ? 'success' : 'danger'"
                disable-transitions>{{scope.row.status=='1' ? '' : ''}}</el-tag>
            </template>
          </el-table-column>
          <el-table-column label="" width="165">
            <template slot-scope="scope">
              <el-tooltip content="" placement="top">
                <img src="../img/compile.png" @click="handleUpdate(scope.$index,scope.row)" />
              </el-tooltip>
              <el-tooltip content="" placement="top" v-if="!hasRole(scope.row)">
                <img src="../img/see.png" @click="handleView(scope.$index,scope.row)" />
              </el-tooltip>
              <el-tooltip content="/" placement="top" v-if="!hasRole(scope.row)">
                <img src="../img/delete.png" @click="handleDelete(scope.$index,scope.row)" />
              </el-tooltip>
            </template>
          </el-table-column>
        </el-table>
      </el-tab-pane>
      <!-- ---end -->
      <!-- ---begin -->
      <el-tab-pane label="" name="second" border v-loading.body="tableLoading" element-loading-text="Loading">
        <!-- ---begin -->
        <el-button class="uploadingBtn" @click="memberAdd"></el-button>
        <!-- ---end -->
        <el-table :data="tableData" border>
          <el-table-column prop="fruitType" :formatter="formatterFruitType" label=""></el-table-column>
          <el-table-column prop="fruitName" label=""></el-table-column>
          <el-table-column prop="fruitPrice" label="">
            <template slot-scope="scope">
              <p>{{scope.row.fruitPrice}}</p>
            </template>
          </el-table-column>
          <el-table-column prop="fruitPacking" label=""></el-table-column>
          <el-table-column prop="fruitNum" label=""></el-table-column>
          <el-table-column label="">
            <template slot-scope="scope">
              <p>{{scope.row.fruitOriginPlace}}</p>
              <p>{{scope.row.fruitSpecifications}}</p>
              <p>{{scope.row.fruitQuality}}</p>
            </template>
          </el-table-column>
          <el-table-column prop="fruitPictureUrl" label="">
            <template slot-scope="scope">
              <img :src="scope.row.fruitPictureUrl" width="90%" height="80" />
            </template>
          </el-table-column>
          <!-- <el-table-column prop="status" label="">
            <template  slot-scope="scope" >
              <el-tag :type="scope.row.status=='1' ? 'success' : 'danger'"
              disable-transitions>{{scope.row.status=='1' ? '' : ''}}</el-tag>
            </template>
          </el-table-column> -->
          <el-table-column label="" width="120">
            <template slot-scope="scope">
              <el-tooltip content="" placement="top">
                <img src="../img/compile.png" @click="handleMemberUpdate(scope.$index,scope.row)" />
              </el-tooltip>
              <!-- <el-tooltip content="/" placement="top" v-if="!hasRole(scope.row)">
                <img src="../img/delete.png" @click="handleDelete(scope.$index,scope.row)" />
              </el-tooltip> -->
            </template>
          </el-table-column>
        </el-table>
      </el-tab-pane>
      <!-- ---end -->
      <!-- ---begin -->
      <el-pagination background @size-change="handleSizeChange" @current-change="handleCurrentChange" :current-page="tablePage.current"
        :page-sizes="[10, 20, 30, 40, 50]" :page-size="tablePage.size" layout="total, sizes, prev, pager, next, jumper"
        :total="tablePage.total">
      </el-pagination>
      <!-- ---end -->
      <!---->
      <el-dialog title="" :visible.sync="dialogFormAdd" width="40%" v-dialogDrag center>
        <el-form :rules="rules" ref="dataForm" :model="temp" label-position="left" label-width="140px">
          <el-form-item label="ID" prop="fruitId">
            <el-select style="width:200px;" v-model="temp.fruitId" placeholder="">
              <el-option v-for="item in fruitId" :key="item" :lable="item" :value="item"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="surplusNum">
            <el-input v-model="temp.surplusNum"></el-input>
          </el-form-item>
          <el-form-item label="" prop="todaySurplusNum">
            <el-input v-model="temp.todaySurplusNum" disabled="disabled"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormAdd = false"></el-button>
          <el-button type="primary" @click="affirmAdd()"></el-button>
        </div>
      </el-dialog>
      <!---->
      <el-dialog title="" :visible.sync="dialogFormUpdate" width="40%" v-dialogDrag center>
        <el-form :rules="rules" ref="dataForm" :model="temp" label-position="left" label-width="140px">
          <el-form-item label="ID" prop="fruitId">
            <el-select style="width:200px;" v-model="temp.fruitId" placeholder="" disabled="disabled">
              <el-option v-for="item in fruitId" :key="item" :lable="item" :value="item"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="" prop="surplusNum">
            <el-input v-model="temp.surplusNum"></el-input>
          </el-form-item>
          <el-form-item label="" prop="todaySurplusNum">
            <el-input v-model="temp.todaySurplusNum" disabled="disabled"></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="dialogFormUpdate = false"></el-button>
          <el-button type="primary" @click="affirmUpdateAdd()"></el-button>
        </div>
      </el-dialog>
    </el-tabs>
  </div>
</template>
<script>
  import fruitApi from '@/api/fruit'
  import {pageParamNames,confirm,root} from '@/utils/constants'
  import {updateCommodity} from "@/utils/commodity"
  import debounce from 'lodash/debounce'
  import {parseTime,resetTemp} from '@/utils'
  import {getToken} from '@/utils/auth'
  import axios from 'axios'
  import store from '@/store'
  export default {
    name: 'commodity',
    data() {
      return {
        updateCommodity: updateCommodity,
        activeName: 'first',
        tableLoading: false,
        query: '',
        pagenum: 1,
        pagesize: 20,
        value: true,
        tableData: [],
        tableQuery: {
          fruitName: null,
          statusValue: null,
          fruitType: null,
        },
        tablePage: {
          current: null,
          pages: null,
          size: null,
          total: null
        },
        idx: null, //
        fruitType: null, //
        fruitName: null, //
        fruitPrice: null, //
        fruitPacking: null, //
        fruitNum: null, //
        fruitOriginPlace: null, //
        fruitPictureUrl: null, //
        status:null,//
        dialogFormAdd: false, //
        dialogFormUpdate: false, //

        rules: {
          fruitName: [{
            validator: validateContent,
            trigger: 'blur'
          }]
        },
        temp: {
          idx: null, //tableData
          id: null,
          fruitId: null, //ID
          surplusNum: null, //
          todaySurplusNum: 10, //
        },
        fruitId: [],
      }
      let validateContent = (rule, value, callback) => {
        if (value === '') {
          callback(new Error(''));
        } else {
          callback();
        }
      };
    },
    // 
    created() {
      this.fetchData()
      this.initData()
    },
    watch: {
      //
      'tableQuery.fruitName': debounce(function() {
        this.fetchData()
      }, 500),
      'tableQuery.statusValue': debounce(function() {
        this.fetchData()
      }, 500),
    }, //watch
    methods: {
      initData() {
        fruitApi.queryIds().then(res => {
          this.fruitId = res.data.data
        })
      },
      hasRole(row) {
        return row && row.rval == root.rval
      },
      // 
      uploadingBtn() {
        this.$router.push({
          path: 'addFruit'
        });
      },
      handleClick(tab, event) {
        this.fetchData()
      },
      // 
      handleSizeChange(val) {
        this.tablePage.size = val;
        this.fetchData();
      },
      handleCurrentChange(val) {
        this.tablePage.current = val;
        this.fetchData();
      },
      fetchData() {
        this.tableLoading = true
        if (this.tableQuery.statusValue != null) {
          let status = this.tableQuery.statusValue;
          this.tableQuery.fruitType = this.getCommodityStatus(status);
        }
        if (this.activeName == 'first') {
          fruitApi.queryFruit(this.tableQuery, this.tablePage).then(res => {
            console.log(res.data)
            this.tableData = res.data.data.data
            this.tableLoading = false
            //
            pageParamNames.forEach(name => this.$set(this.tablePage, name, res.data.data[name]))
          })
        }else if (this.activeName == 'second') {
          fruitApi.queryMembershipBenefits(this.tableQuery, this.tablePage).then(res => {
            console.log(res.data)
            this.tableData = res.data.data.data
            this.tableLoading = false
            //
            pageParamNames.forEach(name => this.$set(this.tablePage, name, res.data.data[name]))
          })
        }
      },
      formatterFruitType: function(row, column, value) {
        for (let i = 0; i < updateCommodity.length; i++) {
          if (value == updateCommodity[i].value) {
            return updateCommodity[i].lable;
          }
        }
      },
      // 
      getCommodityStatus: function(value) {
        for (let i = 0; i < updateCommodity.length; i++) {
          if (value == updateCommodity[i].lable) {
            return updateCommodity[i].value;
          }
        }
      },
      //
      handleUpdate(idx, row) {
        this.$router.push({
          name: 'updateFruit',
          params: {
            id: row.id
          }
        });
      },
      //
      handleView(idx, row) {
        this.$router.push({
          name: 'selectFruit',
          params: {
            id: row.id
          }
        });
      },
      //
      handleDelete(idx, row) {
        let massge="";
        let status;
        if(row.status=='-1'){
          massge="";
          status=1;
        }else{
          massge= "";
          status=-1;
        }
        this.$confirm(massge, '', confirm).then(() => {
          fruitApi.deleteFruit({id: row.id}).then(res => {
            if (res.data.code == 1000 && row.status=='1') {
              this.$message.success('');
            } else {
              this.$message.success('');
            }
            window.location.reload();
          })
        }).catch(() => {
          this.$message.info('/')
        })
      },
      // 
      memberAdd() {
        this.initData()
        this.dialogFormAdd = true
        this.$nextTick(() => {
          this.$refs['dataForm'].clearValidate()
        })
      },
      // 
      affirmAdd() {
        this.$refs['dataForm'].validate((valid) => {
          if (!valid) return
          let formData = new FormData();
          //
          formData.append('fruitId', this.temp.fruitId)
          formData.append('surplusNum', this.temp.surplusNum)
          var url = '/membershipBenefits/add';
          let config = {
            headers: {
              'Content-Type': 'multipart/form-data'
            }
          };
          if (store.getters.token) {
            config.headers['authorization'] = getToken() // token-- ['X-Token']key 
          }
          axios.defaults.withCredentials = true
          axios.post(process.env.BASE_API + url, formData, config, {
            withCredentials: true
          }).then(res => {
            console.log(res.data)
            this.dialogFormAdd = false
            if (res.data.code == 1000) {
              this.$message.success("")
              window.location.reload();
            } else {
              this.$message.error("")
            }
          });
        })
      },
      // 
      handleMemberUpdate(idx, row) {
        this.temp = Object.assign({}, row) // copy obj
        this.temp.idx = idx
        this.dialogFormUpdate = true
        this.$nextTick(() => this.$refs['dataForm'].clearValidate())
      },
      // 
      affirmUpdateAdd() {
        this.$refs['dataForm'].validate((valid) => {
          if (!valid) return
          let formData = new FormData();
          //
          formData.append('id', this.temp.id)
          formData.append('surplusNum', this.temp.surplusNum)
          var url = '/membershipBenefits/update';
          let config = {
            headers: {
              'Content-Type': 'multipart/form-data'
            }
          };
          if (store.getters.token) {
            config.headers['authorization'] = getToken() // token-- ['X-Token']key 
          }
          axios.defaults.withCredentials = true
          axios.post(process.env.BASE_API + url, formData, config, {
            withCredentials: true
          }).then(res => {
            console.log(res.data)
            this.dialogFormUpdate = false
            if (res.data.code == 1000) {
              this.$message.success("")
              window.location.reload();
            } else {
              this.$message.error("")
            }
          });
        })
      },
    },
  };
</script>
<style scoped>
  .commodity {
    position: relative;
  }

  .inquire {
    position: absolute;
    right: 0;
    top: -66px;
    margin-bottom: 20px;
  }

  .uploadingBtn {
    padding: 12px 15px;
    background-color: #D8D8D8;
    font-size: 14px;
    color: #000;
    position: absolute;
    right: 0;
    top: 0;
    z-index: 99
  }
</style>



